.listOfArry > .container{

}
.listOfArry .col-6{
    margin-bottom: 30px;
}
.col-6{
    -webkit-box-flex: 0;
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    padding: 0 15px;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.listOfArry{
    padding: 60px 0;
}
.each_friend_wrp{
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
    position: relative;
    align-items: center;
}
.friendImg{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: #2196f3;   
}
.friendImg > img{
    width: 100%;
    display: block;
    border-radius: 50%;
    height: 100px;
    object-fit: cover;
}
.friendDtls{
    padding-left: 20px;
    -webkit-box-flex: 1;
    flex: 1 0 0;
}
.friendDtls h4{
    font-size: 20px;

}
.addToFirend {
    position: absolute;
    top: 20px;
    right: 20px;
    color: #2196f3;
    cursor: pointer;
    font-size: 20px;
}