.profileWrapper{
	display: flex;
}
.profileImg{
	width: 100px;
    margin: 0 auto;   
    position: relative;
}
.profileImg > img{
	width: 100%;
	height: 100px;
	object-fit: cover;
	border-radius: 50%;
    border: 4px solid #fff;
}
.profileLeft{
    max-width: 300px;
    min-height: 100vh;
    background: #002e52;
    padding: 20px;
    box-sizing: border-box;
    flex: 0 0 300%;
}
.profileIdentity h4{
    font-size: 18px;
    color: #fff;
    text-align: center;
}
.about_auth{
    margin-top: 30px;
}
.about_auth ul li{
    color: rgb(255, 255, 255);
    margin-bottom: 24px;
    padding-left: 40px;
    position: relative;
}
.about_auth ul li i{
    width: 30px;
    height: 30px;
    border: 1px solid #2196f3;
    line-height: 28px;
    text-align: center;
    border-radius: 50%;
    background: #2196f3;
    color: #ffffff;
    margin-right: 10px;
    position: absolute;
    top: 0;
    left: 0;
}
.addRquestBtn{
    margin-top: 30px;
    text-align: center;
}
.sendMessage{
    margin-top: 10px;
    text-align: center;
}
.addRquestBtn a, .sendMessage a{    
    padding: 10px 30px;
    display: inline-block;
    border-radius: 30px;
    text-align: center;
    min-width: 185px;
}
.addRquestBtn a{
    background: #2196f3;
    color: #ffffff;
}
.sendMessage a{
    background: #ffffff;
    color: #002e52;
}
.addRquestBtn a:hover{
    background: #333;
    color: #fff;
}
.sendMessage a:hover{
    background: #333;
    color: #fff;
}

.sendMessage a i, .addRquestBtn a i{
    margin-right: 10px;
}
.profileRight{
    -webkit-box-flex: 1;
    -ms-flex: 1 0 0;
    flex: 1 0 0;
    padding: 0 30px;
}
.tabOptions ul{
    display: flex;
    margin-top: 20px;
    border-bottom: 1px solid #dadada;
}
.tabOptions ul li + li{
    margin-left: 10px;
}
.tabOptions ul li a{
    padding: 10px 20px;
    color: #002e52;
    font-weight: 600;
    border-radius: 4px 4px 0 0;
}
.tabOptions ul li a.active{
    background: #2196f3;
}
.editIcon {
    color: #fff;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    text-align: center;
    line-height: 36px;
    background: #2196f3;
}
.profileInput{
    position: absolute;
    opacity: 0;
}
.editProfileImg{
    width: 30px;
    height: 30px;
    position: absolute;
    right: -4px;
    bottom: 18px;
    border-radius: 50%;
    background-color: #2196f3;
    color: #fff;
    text-align: center;
    line-height: 30px;
    cursor: pointer;
}