@charset "utf-8";
/* CSS Document */
@import url('https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.css');

* {
	margin: 0;
	padding: 0;
	-moz-box-sizing: border-box;
	-ms-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-o-box-sizing: border-box;
	box-sizing: border-box;
	text-rendering: optimizeLegibility;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, caption, section {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
}

html {
	min-height: 100%;
	position: relative;
}

body {
	margin: 0px;
	padding: 0px;
	-webkit-font-smoothing: antialiased;
	word-wrap: break-word;
	color: #5b5a5a;
  	font-family: 'Oxygen', sans-serif;
	font-size: 16px;
	background: #fff;
	line-height: 1.5;
	font-weight: 400;
}

/*--Custom-css---*/

a, button, input[type="submit"] {
	-webkit-transition: all 300ms ease-in-out;
	-moz-transition: all 300ms ease-in-out;
	-ms-transition: all 300ms ease-in-out;
	-o-transition: all 300ms ease-in-out;
	transition: all 300ms ease-in-out;
	outline: none;
	display: inline-block;
	text-decoration: none;
	cursor: pointer;
}

a:hover {
	text-decoration: none;
}

ul {
	list-style: none;
}

.clear {
	clear: both;
}

h1 {
	clear: both;
	color: #3c3c3c;
	font-size: 40px;
	padding: 0;
	font-weight: 700;
}

h2 {
	color: #3c3c3c;
	font-size: 36px;
	padding: 0 0 15px;
	position: relative;
	font-weight: 700;
}

h3 {
	color: #3c3c3c;
	font-size: 26px;
	padding-bottom: 15px;
	font-weight: 700;
}

h4 {
	color: #3c3c3c;
	font-size: 22px;
	padding-bottom: 10px;
	font-weight: 700;
}

h5 {
	color: #3c3c3c;
	font-size: 20px;
	padding-bottom: 10px;
	font-weight: 700;
}

h6 {
	color: #3c3c3c;
	font-size: 14px;
	padding-bottom: 5px;
	font-weight: 700;
}
.container{
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
}
.d-flex{
  display: -ms-flexbox;
  display: flex;
}
.text-center{
  text-align: center;
}




/* //for login container */

.loginSection {
	min-height: calc(100vh - 137px);
}

.showPassowrd {
	position: absolute;
	top: 10px;
	right: 8px;
	color: #a5a5a5;
	z-index: 2;
}

.loginCont {
	max-width: 550px;
}

.loginInner {
	background: #ffffff;
	padding: 30px;
}

.loginTitle h2 {
	font-size: 30px;
  color: #906fff;
	text-transform: uppercase;
	padding-bottom: 20px;
}

.logIconLabel {
	width: 50px;
	height: 50px;
	line-height: 50px;
	background: #8a69ff;
	text-align: center;
	color: #fff;
	position: absolute;
	top: 0;
	left: 0;
}

.loginFormGroup {
	margin-bottom: 1em;
	padding-left: 50px;
	position: relative;
	width: 100%;
	float: left;
}

.loginInput {
	width: 100%;
	height: 50px;
	line-height: 40px;
	border: none;
	border: 1px solid #e0e0e0;
	padding: 0 10px;
  margin: 0;  
  font-size: 16px;
}

.loginInput:focus {
	outline: none;
}

.loginButton {
  background-color: #8a69ff;
  font-weight: 700;
  text-transform: uppercase;
  display: inline-block;
  padding: 0 60px;
  height: 50px;
  line-height: 40px;
  color: #fff;
  border: none;
  cursor: pointer;
}

.forgotPass {
	margin-top: 5px;
	display: block;
	text-align: right;
	font-size: 12px;
	color: #585858;
	font-weight: 500;
}
.notAmember{
  margin-top: 30px;
}
.notAmember p {
	color: #424242;
	font-weight: 500;
}

.notAmember p a {
  color: #8a69ff;
  margin-left: 10px;
}

.logInProfile {
	position: absolute;
	top: 100px;
	right: 60px;
}

.arGooglesignbtn {
	background: #ea4335;
}

.fbSignin {
	background: #3b5999;
}

.orGooglSingsec ul {
	margin: 0 -6px;
}

.orGooglSingsec ul li {
	width: 50%;
	padding: 0 6px;
}

.orGooglSingsec ul li a {
	color: #fff;
	padding: 10px 20px;
	font-size: 14px;
	width: 100%;
	display: block;
}

.orGooglSingsec ul li a i {
	margin-right: 5px;
}

.lrginOrHeading {
	text-align: center;
	font-size: 24px;
	margin-bottom: 20px;
	position: relative;
}

.lrginOrHeading span {
	background: #fff;
	display: inline-block;
	position: relative;
	z-index: 1;
	padding: 0 10px;
}

.lrginOrHeading::before {
	content: "";
	background: rgba(0, 0, 0, 0.2);
	width: 100%;
	height: 1px;
	position: absolute;
	top: 17px;
	left: 0;
}

/* //end login */



